'use client'

import { useEffect, useRef } from 'react'
import Image from 'next/image'

const investors = [
  { name: 'Y Combinator', href: 'https://www.ycombinator.com', logo: '/images/investors/y-combinator.png' },
  { name: 'Bling Capital', href: 'https://www.blingcap.com' },
  { name: 'The Syndicate', href: 'https://www.launchsyndicate.com' },
]

export default function AboutInvestors() {
  const scrollRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const el = scrollRef.current
    if (!el) return

    let frame: number
    const speed = 0.5

    const step = () => {
      el.scrollLeft += speed
      // Reset when we've scrolled through one full set
      if (el.scrollLeft >= el.scrollWidth / 2) {
        el.scrollLeft = 0
      }
      frame = requestAnimationFrame(step)
    }

    frame = requestAnimationFrame(step)
    return () => cancelAnimationFrame(frame)
  }, [])

  return (
    <section className="bg-slate-900">
      <div className="max-w-6xl mx-auto px-4 sm:px-6">
        <div className="py-12 md:py-16">

          <div className="text-center mb-8">
            <h3 className="text-sm font-semibold uppercase tracking-widest text-slate-400">Backed By</h3>
          </div>

          <div
            ref={scrollRef}
            className="overflow-hidden"
          >
            <div className="flex items-center gap-16 md:gap-24 w-max">
              {/* Duplicate the list for seamless loop */}
              {[...investors, ...investors].map((investor, i) => (
                <a
                  key={`${investor.name}-${i}`}
                  href={investor.href}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="shrink-0 flex items-center hover:opacity-80 transition-opacity duration-200"
                >
                  {investor.logo ? (
                    <Image
                      src={investor.logo}
                      alt={investor.name}
                      width={180}
                      height={48}
                      className="h-12 w-auto object-contain grayscale hover:grayscale-0 transition-all duration-300"
                    />
                  ) : (
                    <span className="text-2xl md:text-3xl font-bold font-dm-sans text-slate-500 hover:text-slate-300 transition-colors duration-200 select-none">
                      {investor.name}
                    </span>
                  )}
                </a>
              ))}
            </div>
          </div>

        </div>
      </div>
    </section>
  )
}
